export default {
  name: "ZodiacSelector",
  props: {
    value: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      selectedZodiac: this.value,
      zodiacAnimals: [{
        name: "鼠",
        value: "rat",
        icon: "🐭"
      }, {
        name: "牛",
        value: "ox",
        icon: "🐂"
      }, {
        name: "虎",
        value: "tiger",
        icon: "🐯"
      }, {
        name: "兔",
        value: "rabbit",
        icon: "🐰"
      }, {
        name: "龙",
        value: "dragon",
        icon: "🐲"
      }, {
        name: "蛇",
        value: "snake",
        icon: "🐍"
      }, {
        name: "马",
        value: "horse",
        icon: "🐴"
      }, {
        name: "羊",
        value: "goat",
        icon: "🐑"
      }, {
        name: "猴",
        value: "monkey",
        icon: "🐵"
      }, {
        name: "鸡",
        value: "rooster",
        icon: "🐔"
      }, {
        name: "狗",
        value: "dog",
        icon: "🐶"
      }, {
        name: "猪",
        value: "pig",
        icon: "🐷"
      }]
    };
  },
  methods: {
    selectZodiac(name, value) {
      this.selectedZodiac = value;
      this.$emit('update:value', value);
      this.$emit('select', {
        name,
        value
      });
    },
    // 根据出生年份计算生肖
    calculateZodiac(birthYear) {
      if (!birthYear) return "";

      // 提取年份数字
      let year;
      if (typeof birthYear === 'string') {
        const match = birthYear.match(/\d{4}/);
        if (match) {
          year = parseInt(match[0]);
        } else {
          return "";
        }
      } else if (typeof birthYear === 'number') {
        year = birthYear;
      } else {
        return "";
      }

      // 计算生肖索引 (鼠年为0)
      const zodiacIndex = (year - 4) % 12;
      return this.zodiacAnimals[zodiacIndex].value;
    },
    // 获取生肖名称
    getZodiacName(value) {
      const animal = this.zodiacAnimals.find(a => a.value === value);
      return animal ? animal.name : "";
    },
    // 获取生肖图标
    getZodiacIcon(value) {
      const animal = this.zodiacAnimals.find(a => a.value === value);
      return animal ? animal.icon : "";
    }
  }
};